<template>
  <div class = "table">
    <div class = "crumbs">
      <el-breadcrumb separator = "/">
        <el-breadcrumb-item><i class = "el-icon-lx-cascades"></i>前台数据管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class = "container">
      <div class = "handle-box">
        <!--<el-button type="primary" icon="delete" class="handle-del mr10" @click="delAll">批量删除</el-button>-->
        <!--<el-select v-model="select_cate" placeholder="筛选省份" class="handle-select mr10">-->
        <!--    <el-option key="1" label="广东省" value="广东省"></el-option>-->
        <!--    <el-option key="2" label="湖南省" value="湖南省"></el-option>-->
        <!--</el-select>-->
        <el-input v-model = "select_word" placeholder = "筛选关键词" class = "handle-input mr10"></el-input>
        <el-button type = "primary" icon = "search" @click = "getData">搜索</el-button>
      </div>
      <el-table :data = "goodslist" border class = "table" ref = "multipleTable"
        @selection-change = "handleSelectionChange">
        <el-table-column type = "selection" width = "55" align = "center"></el-table-column>
        <el-table-column prop = "goods_pic" label = "商品图片" width = "">
          <template slot-scope = "scope">
            <div class = "goods_must_img">
              <img :src = "$url+scope.row.goods_pic">
            </div>
            <!--            <span>{{scope.row.gRole[0].name}}</span>-->
          </template>
        </el-table-column>
        <el-table-column prop = "bar_code" label = "条形码" width = "">
        </el-table-column>
        <el-table-column prop = "shop_name" label = "网店名称">
        </el-table-column>
        <el-table-column prop = "goods_number" label = "厂家货号">
        </el-table-column>
        <el-table-column prop = "supplier" label = "供应商">
        </el-table-column>
        <el-table-column prop = "question" label = "客诉问题点">
        </el-table-column>
        <el-table-column prop = "question_analysis" label = "问题点分析">
        </el-table-column>
        <el-table-column prop = "question_solutions" label = "问题点处理方案">
        </el-table-column>
        <el-table-column prop = "arrival_date" label = "最后问题批次到货日期">
        </el-table-column>
        <el-table-column prop = "feedback_date" label = "客诉首次反馈日期">
        </el-table-column>
        <el-table-column prop = "customer_id" label = "客户ID">
        </el-table-column>
        <el-table-column label = "操作" width = "" align = "center">
          <template slot-scope = "scope">
            <el-button type = "text" icon = "el-icon-edit" @click = "handleEdit(scope.$index, scope.row)">编辑/详情
            </el-button>
            <el-button type = "text" icon = "el-icon-delete" class = "red"
              @click = "handleDelete(scope.$index, scope.row)">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--分页器-->
      <div class = "pagination">
        <el-pagination
          background
          :pager-count = "7"
          @prev-click = "previousPage"
          @next-click = "nextClick"
          @current-change = "handleCurrentPageChange"
          layout = "prev, pager, next"
          :total = "pages*10">
        </el-pagination>
      </div>
    </div>
    <!-- 编辑弹出框 -->
    <el-dialog title = "编辑" :visible.sync = "editVisible" width = "50%">
      <el-form ref = "form" :model = "formAdd" label-width = "170px">
        <el-form-item label = "商品主图">
          <div class = "goods_img">
            <img :src = "$url+formAdd.goods_pic" alt = "">
            <el-upload
              class = "upload-demo"
              :action = "$url+'/api/admin/file/pic'"
              name = "image"
              :on-success = "handlesuccess2"
              :on-remove = "handleRemove2"
              :limit = "1"
              :file-list = "fileList2"
              list-type = "picture">
              <el-button size = "small" type = "primary">点击上传</el-button>
            </el-upload>
          </div>
          <!--<el-input v-model = "form.name" placeholder = '输入姓名'></el-input>-->
        </el-form-item>
        <el-form-item label = "条形码">
          <el-input v-model = "formAdd.bar_code" :placeholder = "goodsDetails.bar_code"></el-input>
        </el-form-item>
        <el-form-item label = "网店名称">
          <el-input v-model = "formAdd.shop_name" :placeholder = "goodsDetails.shop_name"></el-input>
        </el-form-item>
        <el-form-item label = "厂家货号">
          <el-input v-model = "formAdd.goods_number" :placeholder = "goodsDetails.goods_number"></el-input>
        </el-form-item>
        <el-form-item label = "供应商">
          <el-input v-model = "formAdd.supplier" :placeholder = "goodsDetails.supplier"></el-input>
        </el-form-item>
        <el-form-item label = "客诉问题点">
          <el-input v-model = "formAdd.question" :placeholder = "goodsDetails.question"></el-input>
        </el-form-item>
        <el-form-item label = "问题点分析">
          <el-input v-model = "formAdd.question_analysis" :placeholder = "goodsDetails.question_analysis"></el-input>
        </el-form-item>
        <el-form-item label = "最后问题批次到货日期">
          <el-date-picker
            v-model = "formAdd.arrival_date"
            type = "datetime"
            :placeholder = "formAdd.arrival_date">
          </el-date-picker>
          <!--<el-input v-model = "form.address"></el-input>-->
        </el-form-item>
        <el-form-item label = "客诉首次反馈日期">
          <el-date-picker
            v-model = "formAdd.feedback_date"
            type = "datetime"
            :placeholder = "formAdd.feedback_date">
          </el-date-picker>
          <!--<el-input v-model = "form.address"></el-input>-->
        </el-form-item>
        <el-form-item label = "客户ID">
          <el-input v-model = "formAdd.customer_id" :placeholder = "goodsDetails.customer_id"></el-input>
        </el-form-item>
        <el-form-item label = "问题图片1">
          <div class = "goods_problem_img">
            <viewer :images = "images">
              <img :src = "$url+formAdd.question_pic1" alt = "" v-if = "formAdd.question_pic1 != null">
            </viewer>
            <span v-if = "formAdd.question_pic1 == null">暂无上传图片</span>
          </div>
          <!--<el-input v-model = "form.address"></el-input>-->
        </el-form-item>
        <el-form-item label = "问题图片2">
          <div class = "goods_problem_img">
            <viewer :images = "images">
              <img :src = "$url+formAdd.question_pic2" alt = "" v-if = "formAdd.question_pic2 != null">
            </viewer>
            <span v-if = "formAdd.question_pic2 == null">暂无上传图片</span>
            <el-upload
              class = "upload-demo"
              :action = "$url+'/api/admin/file/pic'"
              name = "image"
              :on-success = "handlesuccess"
              :on-remove = "handleRemove"
              :limit = "4"
              :file-list = "fileList"
              list-type = "picture"
            >
              <el-button size = "small" type = "primary">点击上传</el-button>
            </el-upload>
          </div>
          <!--<el-input v-model = "form.address"></el-input>-->
        </el-form-item>
        <el-form-item label = "问题图片3">
          <div class = "goods_problem_img">
            <viewer :images = "images">
              <img :src = "$url+formAdd.question_pic3" alt = "" v-if = "formAdd.question_pic3 != null">
            </viewer>
            <span v-if = "formAdd.question_pic3 == null">暂无上传图片</span>
          </div>
          <!--<el-input v-model = "form.address"></el-input>-->
        </el-form-item>
        <el-form-item label = "问题图片4">
          <div class = "goods_problem_img">
            <viewer :images = "images">
              <img :src = "$url+formAdd.question_pic4" alt = "" v-if = "formAdd.question_pic4 != null">
            </viewer>
            <span v-if = "formAdd.question_pic4 == null">暂无上传图片</span>
          </div>
          <!--<el-input v-model = "form.address"></el-input>-->
        </el-form-item>
        <el-form-item label = "备注1">
          <el-input v-model = "formAdd.remark1" :placeholder = "goodsDetails.remark1"></el-input>
        </el-form-item>
        <el-form-item label = "备注2">
          <el-input v-model = "formAdd.remark2" :placeholder = "goodsDetails.remark2"></el-input>
        </el-form-item>
        <el-form-item label = "备注3">
          <el-input v-model = "formAdd.remark3" :placeholder = "goodsDetails.remark3"></el-input>
        </el-form-item>
        <el-form-item label = "备注4">
          <el-input v-model = "formAdd.remark4" :placeholder = "goodsDetails.remark4"></el-input>
        </el-form-item>
        <el-form-item label = "视频">
          <div class = "goods_mp4">
            <video :src = "$url+formAdd.video" controls = "controls">
            </video>
            <el-upload
              class = "upload-demo"
              :action = "$url+'/api/admin/file/video'"
              name = "video"
              :on-success = "handlesuccess1"
              :on-remove = "handleRemove1"
              :limit = "1"
              :file-list = "fileList1"
              list-type = "picture">
              <el-button size = "small" type = "primary">点击上传</el-button>
            </el-upload>
          </div>
          <!--<el-input v-model = "form.address" :placeholder="goodsDetails.bar_code"></el-input>-->
        </el-form-item>
      </el-form>
      <span slot = "footer" class = "dialog-footer">
                <el-button @click = "editVisible = false">取 消</el-button>
                <el-button type = "primary" @click = "saveEdit">确 定</el-button>
            </span>
    </el-dialog>
    <!-- 删除提示框 -->
    <el-dialog title = "提示" :visible.sync = "delVisible" width = "300px" center>
      <div class = "del-dialog-cnt">删除不可恢复，是否确定删除？</div>
      <span slot = "footer" class = "dialog-footer">
                <el-button @click = "delVisible = false">取 消</el-button>
                <el-button type = "primary" @click = "deleteRow">确 定</el-button>
            </span>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'basetable',
    data() {
      return {
        url: '',
        tableData: [],
        cur_page: 1,
        multipleSelection: [],
        select_cate: '',
        select_word: '', // 搜索框
        del_list: [],
        is_search: false,
        editVisible: false,
        delVisible: false,
        form: {
          name: '',
          date: '',
          address: ''
        },
        idx: -1,
        page: 1, // 分页器
        pages: '', //分页器计算
        goodslist: [], //商品数据
        goodsId: '',//接收对应商品的Id
        goodsDetails: {}, // 商品详情
        formAdd: {}, // 数据修改提交
        fileList2: [], //主图的图片上传
        fileList1: [], // 视频上传
        fileList: [], // 问题图片上传
        arrival_date: '',//最后问题批次到货日期
        feedback_date: '', // 客诉首次反馈日期
      }
    },
    created() {
      this.getData();
    },
    computed: {
      data() {
        return this.tableData.filter((d) => {
          let is_del = false;
          for (let i = 0; i < this.del_list.length; i++) {
            if (d.name === this.del_list[i].name) {
              is_del = true;
              break;
            }
          }
          if (!is_del) {
            if (d.address.indexOf(this.select_cate) > -1 &&
              (d.name.indexOf(this.select_word) > -1 ||
                d.address.indexOf(this.select_word) > -1)
            ) {
              return d;
            }
          }
        })
      }
    },
    methods: {
      //获取数据列表
      getData() {
        let params = {
          page: this.page,
          list_rows: 12,
          search: this.select_word,
        }
        this.$axios.post('/api/admin/data/datalistlike', this.$qs.stringify(params), {}).then(res => {
          this.goodslist = res.data.data.data
          this.pages = Math.ceil(res.data.data.total / res.data.data.listRows)
          console.log(res);
        }).catch(err => {
          console.log(err);
        })
      },
      //根据id查询单个数据
      queryIndividualData() {
        let params = {
          id: this.goodsId,
        }
        this.$axios.post('/api/admin/data/datagetone', this.$qs.stringify(params), {}).then(res => {
          this.formAdd = res.data.data
          console.log(res);
        }).catch(err => {
          console.log(err);
        })
      },
      formatter(row, column) {
        return row.address;
      },

      filterTag(value, row) {
        return row.tag === value;
      },
      //点击打开编辑框
      handleEdit(index, row) {
        this.idx = index;
        const item = this.goodslist[index];
        this.goodsId = item.id
        this.editVisible = true;
        this.queryIndividualData()
      },
      //点击打开删除提示框
      handleDelete(index, row) {
        this.idx = index;
        this.delVisible = true;
      },
      /*全部删除*/
      delAll() {
        const length = this.multipleSelection.length;
        let str = '';
        this.del_list = this.del_list.concat(this.multipleSelection);
        for (let i = 0; i < length; i++) {
          str += this.multipleSelection[i].name + ' ';
        }
        this.$message.error('删除了' + str);
        this.multipleSelection = [];
      },

      handleSelectionChange(val) {
        this.multipleSelection = val;
      },

      // 保存编辑
      saveEdit() {
        this.onSubmit()
        //this.$set(this.tableData, this.idx, this.form);
        this.getData()
        this.editVisible = false;
        this.$message.success(`修改第 ${this.idx + 1} 行成功`);
      },
      //修改项
      onSubmit() {
        console.log(this.params);
        console.log(this.fileList);
        console.log(this.fileList1);
        console.log(this.arrival_date);
        console.log(this.feedback_date);
        let params = {
          id: this.formAdd.id, // 商品ID
          goods_pic: this.fileList2.length == 0 ? this.formAdd.goods_pic : this.fileList2, // 头部图片
          bar_code: this.formAdd.bar_code,
          shop_name: this.formAdd.shop_name,
          goods_number: this.formAdd.goods_number,
          supplier: this.formAdd.supplier,
          question: this.formAdd.question,
          question_analysis: this.formAdd.question_analysis,
          question_solutions: this.formAdd.question_solutions,
          arrival_date: this.$moment(this.formAdd.arrival_date).format('YYYY-MM-DD hh:mm:ss'),
          feedback_date: this.$moment(this.formAdd.feedback_date).format('YYYY-MM-DD hh:mm:ss'),
          customer_id: this.formAdd.customer_id,
          question_pic: this.fileList.length == 0 ? [] : this.fileList, // 图片链接数组
          remark1: this.formAdd.remark1,
          remark2: this.formAdd.remark2,
          remark3: this.formAdd.remark3,
          remark4: this.formAdd.remark4,
          video: this.fileList1.length == 0 ? this.formAdd.video : this.fileList1, //视频链接
        }
        this.$axios.post('/api/admin/data/datachange', params, {}).then(res => {
          this.$message({
            message: '上传成功',
            type: 'success'
          });
          this.formAdd = {}
          this.formAdd.arrival_date = ''
          this.formAdd.feedback_date = ''
          this.fileList = []
          this.fileList1 = []
          this.fileList2 = []
          this.getData()
          console.log(res);
        }).catch(err => {
          console.log(err);
        })
      },
      // 确定删除
      deleteRow() {
        //this.tableData.splice(this.idx, 1);
        //this.$message.success('删除成功');
        //this.delVisible = false;
        let params = {
          id: this.goodslist[this.idx].id
        }
        this.$axios.post('/api/admin/data/datadelete', params, {}).then(res => {
          this.getData()
          this.$message.success('删除成功');
          this.delVisible = false;
          console.log(res);
        }).catch(err => {
          console.log(err);
        })
      },
      //分页器
      //点击上一页
      previousPage() {
        this.page--
        this.getData()
      },
      //点击下一页
      nextClick() {
        this.page++
        this.getData()
      },
      //当点击分页器页数的时候
      handleCurrentPageChange(val) {
        this.page = val
        this.getData()
      },
      //弹出层中主图的修改
      //上传成功的钩子
      handlesuccess2(response, file, fileList2) {
        this.fileList2 = fileList2
        console.log(fileList2);
      },
      //移除事件
      handleRemove2(file, fileList2) {
        this.fileList2 = fileList2
        console.log(fileList2);
      },
      //问题图片上传钩子
      handlesuccess(response, file, fileList) {
        this.fileList = fileList
        console.log(fileList);
      },
      //移除事件
      handleRemove(file, fileList) {
        this.fileList = fileList
        console.log(fileList);
      },
      //视频上传钩子
      handlesuccess1(response, file, fileList1) {
        this.fileList1 = fileList1
        console.log(fileList1);
      },
      //移除事件
      handleRemove1(file, fileList1) {
        this.fileList1 = fileList1
        console.log(fileList1);
      },
    }
  }
</script>
<style scoped lang = "scss">
  .handle-box {
    margin-bottom: 20px;
  }
  
  .handle-select {
    width: 120px;
  }
  
  .handle-input {
    width: 300px;
    display: inline-block;
  }
  
  .del-dialog-cnt {
    font-size: 16px;
    text-align: center
  }
  
  .table {
    width: 100%;
    font-size: 14px;
  }
  
  .red {
    color: #ff0000;
  }
  
  /*商品主图*/
  .goods_img {
    display: flex;
    align-items: center;
    
    img {
      width: 200px;
      height: 200px;
      margin-right: 20px;
    }
  }
  
  /*问题图片*/
  .goods_problem_img {
    width: 100%;
    height: 200px;
    display: flex;
    
    img {
      width: 200px;
      height: 200px;
      margin-right: 50px;
    }
    
    span {
      width: 100%;
      text-align: left;
      justify-content: flex-start;
    }
  }
  
  /*视频大小*/
  .goods_mp4 {
    video {
      width: 100%;
      
    }
  }
  
  /*列表主图大小*/
  .goods_must_img {
    width: 100%;
    text-align: center;
    
    img {
      margin: 0 auto;
      width: 100px;
      height: 100px;
    }
  }
</style>



